body{
    background-color: #0c4b4b;
    padding: 5px;
}

a,
h2{
    color: #e1a66f;
}

h3,
h4,
h5{
    color: #f7f5f2;
}

.top-bar{
    margin-top: -15px;
    cursor: pointer;
}

.main-content{
    height: 89vh;
    margin-top: -18px;
    overflow-y: scroll;
}

.chat-room-content{
    display: flex;
    flex-direction: row;
    background-color: #577a7d;
    border-radius: 5px;
    height: 34vh;
    width:100%;
    margin-top: 5px;
}

.category-image{
    width: 30vh;
    height: 30vh;
    border-radius: 50%;
    margin: 2vh 0px 0px 1vh;
    background-color: white;
}

.text-section{
    align-self: flex-end;
    /*background-color: white;*/
    width: 60%;
    margin: auto;
    text-align: center;
}

.bottom-nav-bar{
    display: flex;
    width:98%;
    height: max-content;
    bottom:10px;
    position: fixed;
    justify-content: center;
    cursor: pointer;
}

@media (max-width: 450px){
    .chat-room-content{
        height: 20vh;
    }

    .category-image{
        height: 14vh;
        width: 14vh;
        margin: 100vh 0px 0px 1vh;
    }

    h3{
        font-size: 20px;
    }

    h5{
        margin-top: -15px;
        font-size: 8px;
    }
}

.chat-room-content:hover{
    background-color: #445e60dc;
}