body{
    background-color: #0c4b4b;
}

a,
h2{
    color: #e1a66f;
}

h1,
h3,
h4,
h5{
    color: #f7f5f2;
}

.top-bar{
    margin-top: -15px;
}

.profile-main-content{
    height: 90vh;
    margin-top: -18px;
}

.username-and-date{
    width: 100%;
    text-align: center;
}

.username-and-date button{
    margin-top: 10px;
    color: #f7f5f2d5;
    width: fit-content;
}

.username-and-date hr{
    width: 100%
}

.about-section h3, .link-list h3{
    color: #e1a66f;
    margin-bottom: -2px;
}

.link-list{
    width:fit-content;
    margin: auto;
    text-align: center;
}

.link-list a{
    color: #f7f5f2;
    text-decoration: none;
}

.bottom-nav-bar{
    display: flex;
    width:98%;
    height: max-content;
    bottom:10px;
    position: fixed;
    justify-content: center;
}
