body{
    background-color: #0c4b4b;
}

a,
h2{
    color: #e1a66f;
}

h3,
h4,
h5{
    color: #f7f5f2;
}


.top-bar{
    margin-top: -15px;
}

.create-main-content{
    height: 90vh;
    margin-top: -18px;
}

#form {
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: auto;
    padding: 5px; 
    text-align: center;
 }

 #form label{
     margin: auto;
     color: #f7f5f2;
     padding: 5px;
 }

 #form input{
     border-radius: 2px;
     border-width: 0px;
     width: 99%;
 }

 #form textarea{
     border-radius: 2px;
     border-width: 0px;
     height: 140px;
 }

 #form #submit{
     width: 100%;
     margin-top: 10px;
     border-radius: 2px;
     border-width: 0px;
     height: 25px;
     background-color: #e1a66f;
     cursor: pointer;
 }

.bottom-nav-bar{
    display: flex;
    width:98%;
    height: max-content;
    bottom:10px;
    position: fixed;
    justify-content: center;
}

#submit:hover{
    background-color: #e1a66fde;
}

#anon{
    color:#e1a66f9e;
    cursor: pointer;
    margin: auto;
}

@media (min-width: 350px){

    h3{
        font-size: 20px;
    }

    h5{
        margin: -5px;
        font-size: 10px;
    }
}
