body{
    background-color: #0c4b4b;
    overflow-y: 1px;
}

a,
h2{
    color: #e1a66f;
}

h3,
h4,
h5{
    color: #f7f5f2e7;
}

h3{
    padding: 5px;
}

p{
    color: #f7f5f2a1;
    margin-bottom: -3vh;
    font-size: smaller;
    padding: 5px;
}

.top-bar{
    margin-top: -15px;
}

.main-content-d{
    height: 90vh;
    margin-top: -18px;
    overflow-y: hidden;
}

.suggested-section,
.opinion-challanging-section,
.echo-chamber-section{
    display: flex;
    flex-direction: row;
    height: 30vh;
}

.chat-room-suggestion,
.chat-room-suggestion-1{
    display: flex;
    flex-direction: column;
    min-width: 22vh; 
    text-align: center;
    align-items: center;
    border-radius: 10px;
}

.chat-room-suggestion:nth-child(odd){
    background-color: #577a7d;
}

.chat-room-suggestion-1:nth-child(even){
    background-color: #577a7d;
}


.action-anchors{
    position: fixed;
    right: 10px;
}

.action-anchors a{
    font-size: small;
}


.category-image{
    background-color: white;
    height: 8vh;
    width: 8vh;
    margin-top: 3vh;
    border-radius: 50%;
}

.bottom-nav-bar{
    display: flex;
    width:98%;
    height: max-content;
    bottom:10px;
    position: fixed;
    justify-content: center;
}
