body{
    background-color: #0c4b4b;
}

a,
h2{
    color: #e1a66f;
    text-decoration: none;
}

h3,
h4,
h5{
    color: #f7f5f2;
}

.options-bubble button{
    text-align: center;
    width: 75%;
    height: 20px;
    color: #f7f5f2;
    border-radius: 5px;
}

.options-bubble h5{
    color: rgba(255, 0, 0, 0.682);
    padding: 15px;
    cursor: pointer;
}

.top-bar{
    margin-top: -20px;
}

.votes-section{
    position: fixed;
    right: 5px;
    top: 9px;
}

.vote-section button{
    width: fit-content;
    height: 5px;
}

.main-content{
    display: flex;
    flex-direction: column;
    height: 83vh;
    margin-top: -18px;
    width: 100%;
}

.options-bubble{
    position: fixed;
    top: 20%;
    width: 95%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #577a7d;
    border-radius: 15px;
    text-align: center;
    color: white;
    padding: 15px 0 15px 0;
}


.voter{
    cursor: pointer; 
    margin-top: 15px;
    color: white;
    width: 30vh;
    background:#465f61;
    border-radius: 5px;
}


.chat-text-content{
    display: flex;
    flex-direction: column;
}

.chat-text-content p{
    color: #f7f5f2;
    margin: 0 0 -5px 0;
    padding: 15px 0 0 0;
}

.upper{
    display: flex;
    flex-direction: row;
    cursor: pointer;
}


.chat-bubble{
    display: flex;
    max-width: 88vh;
    min-width: max-content;
    width: auto;
    height: fit-content;
    margin-top: 5px;
    background-color: white;
    border-radius: 0px 6px 0px 6px;
}

.text{
    max-width: 88vh;
    padding: 5px 10px 5px 10px;
}

#user-end{
    align-items: end;
}

#user-end-box{
    background-color: gray;
    border-radius: 6px 0 6px 0;
}

.bottom-nav-bar{
    display: flex;
    flex-direction: row;
    width:98%;
    height: max-content;
    bottom:10px;
    position: fixed;
    justify-content: center;
}

input{
    width: 50vh;
    border-radius: 2px;
    border-width: 0px;
}

.bottom-nav-bar button{
    width: fit-content;
    color: #f7f5f2;
    margin-left: 5px;
}

button{
    border-width: 0px;
    border-radius:2px;
    background-color: #e1a66f;
    width: 10vh;
    text-align: center;
}

#votes-button{
    height: 35px;
}

#votes-button p{
    padding: 0px;
    margin: auto;
}

button:hover{
    background-color: #e1a66fde;
}

@media (max-width: 815px){

    h3{
        font-size: 20px;
    }

    h5{
        margin: -5px;
        font-size: 10px;
    }
    
    .chat-bubble{
        display: flex;
        max-width: 5vh;
        min-width: max-content;
        width: auto;
        height: fit-content;
        margin-top: 5px;
        background-color: white;
        border-radius: 0px 6px 0px 6px;
    }
    
    .text{
        max-width: 38vh;
        padding: 5px 10px 5px 10px;
    }

    
    input{
        width: 35vh;
        border-radius: 2px;
        border-width: 0px;
    }
    
    button{
        border-width: 0px;
        border-radius:2px;
        background-color: #e1a66f;
        width: 5vh;
    }

}
